








import Vue from 'vue'
import { Component, Prop, Watch } from 'vue-property-decorator'

import AuthorGeneralCard from '@/pages/authorPage/AuthorGeneralCard.vue'
import { AuthorDto } from '@/services/models'
import api from '@/services/api'

@Component({
  components: {
    AuthorGeneralCard
  }
})
export default class Author extends Vue {
  @Prop({ type: Number, required: true }) id!: number
  author: AuthorDto = new AuthorDto()

  async created() {
    this.author = await api.authors.getById(this.id)
  }

  @Watch('author', {
    deep: true
  })
  async onModelChange(value: AuthorDto, oldValue: AuthorDto) {
    if (oldValue && oldValue.id) {
      await api.authors.update(this.id, value)
    }
  }
}
