

























import Vue from 'vue'
import { Component, Prop } from 'vue-property-decorator'
import { AuthorDto } from '@/services/models.ts'

@Component
export default class AuthorGeneralCard extends Vue {
  @Prop({ type: Object, required: true }) readonly value!: AuthorDto

  onChangeName(value: string) {
    this.value.name = value
  }
  onChangeAge(value: string) {
    this.value.biography = value
  }
}
